import VueRouter from 'vue-router';
// Pages
import Characters from '@/home/views/Characters.vue';
var routePrefix = '';
var routes = [
    {
        path: '*',
        component: Characters
    },
    {
        name: 'characters',
        path: "/" + routePrefix,
        component: Characters
    }
];
export var router = new VueRouter({
    mode: 'history',
    routes: routes,
    linkActiveClass: 'is-active'
});
