import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
var state = {
    character: {}
};
var getters = {
    character: function (state) {
        return state.character;
    }
};
var actions = {
    getCharacterById: function (_a, id) {
        var commit = _a.commit;
        return new Promise(function (resolve, reject) {
            axios
                .get('/iceandfire/api/characters/' + id)
                .then(function (response) {
                var payload = response && response.data;
                commit('SET_CHARACTER', payload);
                resolve(true);
            })
                .catch(function (error) {
                reject(error);
            });
        });
    }
};
var mutations = {
    SET_CHARACTER: function (state, character) {
        state.character = character;
    }
};
var store = {
    state: state,
    getters: getters,
    actions: actions,
    mutations: mutations
};
export default new Vuex.Store(store);
